import React, { Component } from 'react'
import PropTypes from 'prop-types'

export class TodoItem extends Component {
    getStyle = () => {
        return {
            backgroundColor: this.props.todo.completed ? 
             '#12aa12' : '#aaaaaa',
            padding: 10,
            borderBottom: '1px #ccc dotted'
        }
    }

    render() {
        const {id, title} = this.props.todo;
        const {markComplete, delTodo} = this.props;
        return (
            <div style={this.getStyle()}>
                <p>
                    <input type="checkbox" onChange={markComplete.bind(this, id)} />
                    {title}
                    <button style={btnStyle} onClick={delTodo.bind(this, id)}>x</button>
                </p>
            </div>
        )
    }
}

TodoItem.propTypes = {
    todo: PropTypes.object.isRequired,
    markComplete: PropTypes.func.isRequired,
    delTodo: PropTypes.func.isRequired
}


const btnStyle = {
    backgroundColor: '#ff0000',
    color: '#fff',
    border: '#aa0000 solid 1px',
    padding: '5px 9px',
    borderRadius: '50%',
    cursor: 'pointer',
    float: 'right'
}

// const itemStyle = {
//     backgroundColor: '#aaaaaa'
// }

export default TodoItem
